          <td width="25%">${name}</td>
          <td>${''}</td>
          </tr>`;
      
      } else {
        return `<tr>
          <td width="25%">${name}</td>
          <td>${Object.values(item)[i]}</td>
          </tr>`;
      }
    }).join('');
  });
  const body = `<tbody>${rows.join('')}</tbody>`;
  return `<table class="table table-responsive">${body}</table>`
}

